<template>
  <div class="page flex-v">
    <div class="head flex-v-center flex-justify">
      <div class="head-title">体检报告维护</div>

      <div class="flex">
        <el-form size="small"
          inline
          class="default-form-style">
          <el-form-item label="体检流水号："
            style="margin-bottom:0">
            <el-input placeholder="请输入"
              v-model="examUserId"
              clearable></el-input>
          </el-form-item>
          <el-button icon="el-icon-search"
            style="margin: 0 30px"
            size="small"
            @click="getExamReport(examUserId)"
            round>查询</el-button>
        </el-form>
        <div class="head-btn"
          @click="printPage">
          <img src="@/assets/img/d4.png"
            alt="打印">&nbsp;<span>打印</span>
        </div>
      </div>
    </div>

    <div class="container flex-item"
      ref='print-html-box'>
      <medical-report-html style="height:100%"
        :user='user'
        :projectList='projectList'></medical-report-html>
    </div>
  </div>
</template>

<script>
import printMethod from '@/lib/print-method'
import MedicalReportHtml from '@/components/MedicalReportHtml'
export default {
  name: 'MedicalReport',
  components: { MedicalReportHtml },
  data() {
    return {
      examUserId: '',
      reportObj: {},
      user: {},
      projectList: []
    }
  },

  created() {
    // this.getExamReport('101201907080001')
    this.initData()
  },

  methods: {
    initData() {
      let examUserId = this.$route.query.tjsysGuid
      this.$post('/user/examUser/showReportExamList', {
        pageNum: 1,
        pageSize: 1
      })
        .then(({ data }) => {
          if (examUserId) return
          this.getExamReport(data.list[0].sysGuid)
        })
        .catch(err => this.$message.error(err))
      examUserId && this.getExamReport(examUserId)
    },
    printPage() {
      this.$confirm('确定打印视为打印一次！', {
        title: '温馨提示',
        showClose: false,
        customClass: 'msg-box'
      })
        .then(() => {
          let loading = this.$loading({ text: '打印准备中...' })
          this.$post('/user/examUser/printRecord', {
            idList: this.user.examUserId
          })
            .then(() => {
              console.log('统计打印成功')
              printMethod(this.$refs['print-html-box'], '体检报告')
              setTimeout(() => {
                loading.close()
              }, 800)
            })
            .catch(err => {
              loading.close()
              this.$message.error('打印记录失败，无法打印！')
            })
        })
        .catch(cancel => cancel)
    },

    getExamReport(examUserId) {
      if (examUserId.trim() === '') return
      this.$post('/user/examUser/showReportExam', {
        examUserId
      })
        .then(({ data }) => {
          this.user = data.user
          this.projectList = data.smallProjectList
        })
        .catch(err => {
          this.$message.error(err)
          // this.reportObj = { user: {}, smallProjectList: []}
        })
    },

    mergeMethod({ row, column, rowIndex, columnIndex }) {
      console.log(row, column, rowIndex, columnIndex)
      if (columnIndex === 0) {
        if (rowIndex % 3 === 0) return [3, 1]
        else {
          return [0, 0]
        }
      }
    },

    handleData(data) {}
  },
  mounted() {}
}
</script>

<style lang="less" scoped>
.head {
  padding: 0 70px 0 20px;
  height: 70px;
  border-bottom: 1px solid #eaeff5;
  &-title {
    font-size: 24px;
  }
  &-btn {
    line-height: 30px;
    color: #466791;
    cursor: pointer;
    img,
    span {
      vertical-align: middle;
      font-size: 16px;
    }
  }
}
.container {
  overflow-y: auto;
}

.print-foot {
  padding: 0 20px;
  height: 50px;
  line-height: 50px;
  border-top: 1px dashed #eee;
}
</style>



